@import '~antd/dist/antd.css';

.App {
  display: flex;
  flex-direction: column;
}

.header {

  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ccc;
}

.form {
  margin-left: 30px;
}

.logout {
  margin-right: 30px;
}

.addNewForm {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.inputAdd input {
  width: 200px;
}

.btnAdd {
  margin-left: 10px;
}

.todoList {
  margin-top: 10px;
  color: #1890ff;
}

.todoListHeader {
  width: 100%;
  height: 40px;
  background-color: #fafafa;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.todoTitle {
  margin-left: 30px;
}

.todoTitle i {
  margin-right: 10px;
}

.isHow {
  color: white;
  background-color: #1890ff;
  margin-top: 10px;
  padding: 0 7px;
  margin-bottom: 5px;
  font-size: 18px;
  border-radius: 4px 4px 4px 4px;
}

.isHow:hover {
  transition: 0.4s;
  opacity: 0.7;
  cursor: pointer;
}

.buttons button {
  margin: 5px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

tr:nth-of-type(odd) {
  background: #eee;
}

th {
  background: #fafafa;
  color: #1890ff;
  font-weight: bold;
}

td, th {
  padding: 6px;
  border: 1px solid #ccc;
  text-align: left;
}

.taskDone {
  opacity: 0.5;
}

.icon label {
  margin-right: 15px;
}

.wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 20px;
}


.inp {
  color: #1890ff;
  height: 100%;
  font-size: 16px;
}

.inpDate {
  height: 100%;
  font-size: 12px;
}

.select {
  height: 100%;
  font-size: 12px;
  color: #1890ff;
}

.wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 180px;
  color: #1890ff;
  font-size: 24px;
}

.wrapper span:nth-child(3) {
  margin-bottom: 10px;
}

.formRedux {
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.formRedux input {
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 20px;
}

.formRedux button {
  text-transform: uppercase;
  outline: 0;
  background: #1890ff;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  cursor: pointer;
}

.formRedux button:hover, .form button:active, .form button:focus {
  opacity: .5;
}

.loader {
  position: fixed;
  margin: 0 auto;
  top: 40%;
  right: 50%;
  font-size: 40px;
  color: #ff720c;

}

@media screen and (max-width: 800px) {
  .todoListHeader {
    flex-direction: column;
    align-items: center;
    height: 128px;
  }

  .todoTitle {
    margin-left: 0;
  }

  .addedDate {
    margin-right: 0;
  }
}

@media screen and (max-width: 340px) {
  .form {
    margin-left: 10px;
  }

  .logout {
    margin-right: 10px;
  }
}

@media only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px) {
  table, thead, tbody, th, td, tr {
    display: block;
    width: 100%;
  }

  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;

  }

  tr {
    border: 1px solid #ccc;

  }

  td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
    width: 100% !important;
  }

  td:before {
    position: absolute;
    top: 6px;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
  }

  td:nth-of-type(1):before {
    content: "Check";
  }

  td:nth-of-type(2):before {
    content: "Added date";
  }

  td:nth-of-type(3):before {
    content: "Title";
  }

  td:nth-of-type(4):before {
    content: "Description";
  }

  td:nth-of-type(5):before {
    content: "Priority";
  }

  td:nth-of-type(6):before {
    content: "Start date";
  }

  td:nth-of-type(7):before {
    content: "Deadline";
  }

  td:nth-of-type(8):before {
    content: "Delete";
  }
}


